import React, { Component } from 'react';
import { graphql, compose } from 'react-apollo';
import { getEmployersQuery, addJobMutation, getJobsQuery } from '../queries/queries';

class AddJob extends Component {
    constructor(props){
        super(props);
        this.state = {
            name:'',
            category:'',
            employerId:''
        };
    }
    displayEmployers(){
        var data = this.props.getEmployersQuery;
        if(data.loading){
            return(<option disabled>Loading Employers..</option>);
        }else{
            return data.employers.map(employer => {
                return( <option key={ employer.id } value={ employer.id }>{ employer.name }</option>);
            })
        }
    }
    submitForm(e){
        e.preventDefault();
        this.props.addJobMutation({
            variables:{
                name: this.state.name,
                category: this.state.category,
                employerId: this.state.employerId
            },
            refetchQueries:[{ query:getJobsQuery }]
        });
    }
    render() {
        return (
        <form id="add-job" onSubmit={ this.submitForm.bind(this) }>

            <div className="field">
                <label>Job name:</label>
                <input type="text" onChange={ (e)=> this.setState({ name: e.target.value })} />
            </div>

            <div className="field">
                <label>Category:</label>
                <input type="text" onChange={ (e)=> this.setState({ category: e.target.value })} />
            </div>

            <div className="field">
                <label>Employer:</label>
                <select onChange={ (e)=> this.setState({ employerId: e.target.value })}>
                    <option>Select employer</option>
                    { this.displayEmployers() }
                </select>
            </div>

            <button>+</button>
            
        </form>
        );
    }
}

export default compose(
    graphql(getEmployersQuery, {name:"getEmployersQuery"}),
    graphql(addJobMutation, {name:"addJobMutation"})
)(AddJob);


