import { gql } from 'apollo-boost';

const getJobsQuery = gql`
    {
        jobs{
            category
            name
            id
        }
    }
`

const getEmployersQuery = gql`
    {
        employers{
            name
            id
        }
    }
`

const addJobMutation = gql`
    mutation($name:String!, $category:String!, $employerId:ID!){
        addJob(name:$name, category:$category, employerId:$employerId){
            name
            id
        }
    }
`

const getJobQuery = gql`
    query($id: ID){
        job(id:$id){
            id
            name
            category
            employer{
                id
                name
                age
                jobs{
                    name
                    id
                }
            }
        }       
    }
`

export { getEmployersQuery, getJobsQuery, addJobMutation, getJobQuery };


