import React, { Component } from 'react';
import { graphql } from 'react-apollo';
import { getJobQuery } from '../queries/queries';

class JobDetails extends Component {
    displayJobDetails(){
        const { job } = this.props.data;
        if(job){
            return(
                <div>
                    <h3>Job Title: { job.name }</h3>
                    <p>Category: { job.category }</p>
                    <p>Contact: { job.employer.name }</p>
                    <p>All jobs by this employer:</p>
                    <ul className="other-jobs">
                        { job.employer.jobs.map(item => {
                            return <li key={ item.id }>{item.name}</li>
                        })}
                    </ul>
                </div>     
            )
        }else{
            return(
                <div>No job selected...</div>
            )
        }
    }

    render() {

        return (
            <div id="job-details">
                { this.displayJobDetails() }
            </div>
        );
    }
}

export default graphql(getJobQuery,{
    options:(props) => {
        return{
            variables:{
                id:props.jobId
            }
        }
    }
})(JobDetails)


